@import '../../style'
.popup
  &-wrapper
    position: relative
    inset: 0
    width: 100vw
    height: 100vh
    display: flex
    justify-content: center
    align-items: center
    background-color: rgba(0, 0, 0, 0.2)
    z-index: 20

  &__item
    position: absolute
    top: 50%
    transform: translateY(-50%)
    background-color: $white
    padding: 20px 0 0

  &__swipers-wrapper
    display: flex
    max-height: 40px
    margin: 40px auto
    justify-content: center

  &__swiper-wrapper
    position: relative
    display: flex
    align-items: center
    &:first-of-type
      margin-left: -15px
    &:last-of-type
      margin-right: -15px

    &::before,
    &::after
      position: absolute
      display: block
      content: ""
      height: 2px
      width: 56px
      background-color: #e5e5e5

    &:first-of-type
      &::before
        top: -10px
        left: 50%
        transform: translateX(-50%)

      &::after
        bottom: 10px
        left: 50%
        transform: translateX(-50%)

    &:last-of-type
      &::before
        top: -10px
        right: 50%
        transform: translateX(50%)

      &::after
        bottom: 10px
        right: 50%
        transform: translateX(50%)

  &__swiper
    position: relative

  &__title
    margin: 0
    padding-right: 95px
    margin-bottom: 50px

  &__buttons-wrapper
    display: flex

  &__btn
    font-size: 12px
    line-height: 14px
    border: 0.5px solid #e5e5e5
    border-radius: 1px
    padding: 15px 65px
    cursor: pointer
    color: $black
    background-color: transparent

    &--bold
      font-weight: 700

.swiper
  margin-top: -15px
  width: 100px
  height: 100px
  &-slide
    display: flex
    justify-content: center
    align-items: center
    width: 100px

.slide__gray
  &--prev
    background: linear-gradient(180deg, rgba(153, 153, 153, 0) 0%, #999999 47.4%)
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    background-clip: text
    text-fill-color: transparent

  &--next
    background: linear-gradient(180deg, #999999 56.25%, rgba(153, 153, 153, 0) 100%)
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    background-clip: text
    text-fill-color: transparents